<template>
  <div>
    <b-card id="depositcard" header="Depozit Funds">
      <div v-if="pagecount==1">
        <p class="errormessage">{{errors}}</p>
        <b-row>
        <b-col id="imgpayment" cols="7" md="6" sm="12"
          ><b-button @click="paytype('Bitcoin')" variant="light" class="mb-3 mr-1"
            ><img
              style="width: 10rem; height: 10rem"
              src="@/assets/images/payment/bitcoin.png"
          /></b-button>
        </b-col>
        <b-col id="imgpayment" cols="7" md="6" sm="12"
          ><b-button @click="paytype('Ethereum')" variant="light" class="mb-3 mr-1"
            ><img
              style="width: 10rem; height: 10rem"
              src="@/assets/images/payment/ethereum.png"
          /></b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="4">
          <span style="font-size: 18px">Depozit To</span>
        </b-col>
        <b-col cols="8">
          <b-form-group label-for="depozitselect">
            <b-form-select
              plain
              v-model="selectdepozit"
              :options="depozit"
              id="depozitselect"
            >
              <template v-slot:first>
                <b-form-select-option :value="null" disabled
                  >Wallet</b-form-select-option
                >
              </template>
            </b-form-select>
          </b-form-group>
        </b-col>
      </b-row>
      </div>
      <div v-if="pagecount==2">
        <p class="errormessage">{{errors}}</p>
        <b-form-group label="Deposit Amount " label-for="inputdeposit">
                <b-form-input id="inputdeposit" type="text" v-model="depositamount"  ></b-form-input>
        </b-form-group>
        <b-form-group label="Credited Amount " label-for="inputcredit">
                <b-form-input id="inputcredit" type="text" v-model="depositamount" disabled ></b-form-input>
        </b-form-group>
      </div>
      <div v-if="pagecount==3">
        <table class="table table-condensed table-with-buttons">
    <tbody><tr>
        <td>Deposit With</td>
                    <td>{{paymenttype}}</td>
            </tr>
    <tr>
        <td>Deposit To</td>
        <td>Wallet 3</td>
    </tr>
    <tr>
        <td>Deposit Amount</td>
        <td>{{depositamount}}</td>
    </tr>
            <tr>
            <td>Amount To Be Credited</td>
            <td>{{depositamount}}</td>
        </tr>
        </tbody></table>
     </div>
      <b-button v-if="pagecount>=2"  @click="previouspage()" variant="primary">Previous</b-button>
      <b-button v-if="pagecount!=3" @click="nextpage()" class="ml-3" variant="primary">Continue</b-button>
      <b-button v-if="pagecount==3" @click="sendDeposit(depositamount)"  class="ml-3" variant="primary">Deposit</b-button>
    </b-card>
    </div>
</template>
<script>
export default {
  data () {
    return {
      errors: '',
      pagecount: 1,
      selectdepozit: null,
      depositamount: null,
      paymenttype: null,
      depozit: [
        { value: 1300, text: 1300 },
        { value: 1300, text: 1300 },
        { value: 1300, text: 1300 }
      ]
    }
  },
  methods: {
    previouspage () {
      this.pagecount -= 1
    },
    nextpage () {
      switch (this.pagecount) {
        case 1:
          if (this.paymenttype == null) {
            this.errors = 'Yükleme yapmak istediğiniz hesap şeklini seçin'
          } else if (this.selectdepozit == null) {
            this.errors = 'Yükleme yapmak istediğiniz hesabı seçiniz'
          } else {
            this.errors = ''
            this.pagecount += 1
          }
          break
        case 2:
          if (this.depositamount == null) {
            this.errors = 'Lütfen alanları doldurunuz...'
          } else {
            this.errors = ''
            this.pagecount += 1
          }
          break
        default:
          this.pagecount += 1
          break
      }
    },
    paytype (paymentname) {
      this.paymenttype = paymentname
    },
    sendDeposit (amount) {
      this.$store.dispatch('Payment/sendDeposit', amount)
    }
  },
  computed: {
    progressvalue: function () {
      return this.pagecount * 33
    }
  }
}
</script>
<style scoped>
.errormessage{
  margin: auto;
  color: red;
  font-size: 18px;
}
#depositcard {
  justify-content: center;
  align-items: center;
}
#imgpayment {
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 900px) {
  #imgpayment {
    padding-left: 4rem;
  }
}
</style>
